import React, { Component } from 'react';
import { Table } from 'antd';
import { ColEditDelete } from 'components/common';

export default class TableTeacherSubject extends Component {
  state = {};

  columns = [
    {
      title: 'Mã môn học',
      dataIndex: 'subjectId',
      key: 'subjectId',
    },
    {
      title: 'Môn học',
      dataIndex: 'subject',
      key: 'subject',
    },
    {
      title: 'Bộ môn',
      dataIndex: 'subjectGroup',
      key: 'subjectGroup',
    },
    {
      title: 'Loại môn',
      dataIndex: 'subjectType',
      key: 'subjectType',
    },
    {
      title: 'Khối kiến thức',
      dataIndex: 'knowledge',
      key: 'knowledge',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          hidenBtnEdit
          onConfirmDelete={() => this.props.onDelete(record.subjectId)}
        />
      ),
    },
  ];

  render() {
    const { data } = this.props;
    return (
      <Table
        bordered
        scroll={{ x: true }}
        dataSource={data}
        columns={this.columns}
        {...this.props}
      />
    );
  }
}



