import React, { Component, Fragment } from 'react';
import { Card, Row, Col, Spin, message, Form, Input } from 'antd';

import { getStaffs, getStaffById } from 'services/teacher';
import { getStudentSubjectById, createTeacherSubject } from 'services/TeacherSubject';
import { getSubjectEduById } from 'services/subjectEdu';

import Page from 'components/LayoutComponents/Page';
import { getQueryParams } from 'utils/urlUtil';
import { BtnAddNew, BtnSave } from 'components/common/button';
import {
  SelectAjax,
} from 'components/common';

import TableTeacherSubjectDetail from './TableTeacherSubjectDetail';
import ModalAddNewTeacherSubject from './ModalAddNewTeacherSubject';

@Form.create({ name: 'GiangVien-MonHoc' })
class ManualSchedulePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      teacherId: "",
      openModal: false,
      loading: false,
      loadingModal: false,
      data: [],
      informationTeacher: {},
    };
  }

  componentDidMount = () => {
    const queryParams = getQueryParams();
    console.log(queryParams);
    if (queryParams.teacherId) {
      this.setState({
        teacherId: queryParams.teacherId,
      });
      this.setState({
        loading: true,
      });
      this.getDataSubjects(queryParams.teacherId);
      this.getInformationTeacher(queryParams.teacherId);
      this.setState({
        loading: false,
      });
    }
  };

  onSaveTecherSubjects = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const { teacherId, data } = this.state;
      const dataPost = {
        teacherId,
        subjectIds: [],
      };
      let isAddNew = false;
      if (!teacherId) {
        this.props.form.validateFields(async (err, values) => {
          if (err) {
            return;
          }
          isAddNew = true;
          this.setState({
            teacherId: values.teacherId,
          });
          dataPost.teacherId = values.teacherId;
        })
      }
      this.setState({
        loading: true,
      });
      data.forEach((item) => {
        dataPost.subjectIds.push(item.subjectId);
      })
      await createTeacherSubject(dataPost);
      message.success('Lưu thành công.');
      this.setState({
        loading: false,
      }, () => {
        this.getDataSubjects(dataPost.teacherId);
        if (isAddNew) {
          this.getInformationTeacher(dataPost.teacherId);
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  onSaveModal = async (subject) => {
    this.setState({
      loadingModal: true,
    });
    const { data } = this.state;
    const id = subject.subjectId;
    const isDuplicate = data.some((item) => item.subjectId === id);
    if (isDuplicate) {
      message.warn("Môn học đã tồn tại.");
      this.setState({
        loadingModal: false,
      });
    } else {
      const response = await getSubjectEduById(id);
      response.data.subjectId = response.data.id;
      response.data.subject = response.data.label;
      data.push(response.data);
      this.setState({
        data,
        loadingModal: false,
      }, () => {
        this.closeModal();
      });
    }
  }

  onDeleteSubject = (subjectId) => {
    let { data } = this.state;
    data = data.filter(item => item.subjectId !== subjectId);
    this.setState({
      data,
    });
  }

  getDataSubjects = async (teacherId) => {
    try {
      const response = await getStudentSubjectById({ teacherId });
      this.setState({
        data: response.data.subjectList,
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loading: false,
      });
    }
  }

  getInformationTeacher = async (teacherId) => {
    try {
      const id = teacherId;
      const response = await getStaffById(id);
      this.setState({
        informationTeacher: response.data,
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loading: false,
      });
    }
  }

  openModal = () => {
    this.setState({
      openModal: true,
    });
  }

  closeModal = () => {
    this.setState({
      openModal: false,
    });
  }

  render() {
    const {
      teacherId,
      openModal,
      loading,
      data,
      informationTeacher,
      loadingModal
    } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <Fragment>
        <Spin spinning={loading}>
          <Card title="Giảng viên - Môn học chi tiết">
            {teacherId ?
              <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
                <Row gutter={24}>
                  <Col span={8}>
                    <Form.Item label="Giảng viên">{getFieldDecorator('fullname', { initialValue: informationTeacher.fullname })(<Input disabled />)}</Form.Item>
                  </Col>
                  <Col span={8}>
                    <Form.Item label="Giới tính">{getFieldDecorator('genderText', { initialValue: informationTeacher.genderText })(<Input disabled />)}</Form.Item>
                  </Col>
                </Row>
                <Row gutter={24}>
                  <Col span={8}>
                    <Form.Item label="Khoa">{getFieldDecorator('faculty', { initialValue: informationTeacher.faculty })(<Input disabled />)}</Form.Item>
                  </Col>
                  <Col span={8}>
                    <Form.Item label="Bộ môn">{getFieldDecorator('subjectGroup', { initialValue: informationTeacher.subjectGroup })(<Input disabled />)}</Form.Item>
                  </Col>
                </Row>
              </Form>
              :
              <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
                <Row gutter={24}>
                  <Col xs={24} sm={8} md={8}>
                    <Form.Item label="Giảng viên">
                      {getFieldDecorator('teacherId', {
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng chọn giảng viên!',
                          },
                        ],
                      })(
                        <SelectAjax
                          service={getStaffs}
                          keyValue="staffId"
                          keyLabel="fullname"
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Form>
            }

            <Row gutter={24}>
              <Col span={24}>
                <BtnAddNew onClick={this.openModal} />
                <BtnSave onClick={this.onSaveTecherSubjects} />
              </Col>
            </Row>
            <Row gutter={24}>
              <Col span={24}>
                <TableTeacherSubjectDetail
                  data={data}
                  onDelete={this.onDeleteSubject}
                />
              </Col>
            </Row>
          </Card>
        </Spin>
        {openModal &&
          <ModalAddNewTeacherSubject
            visible={openModal}
            closeModal={this.closeModal}
            onSaveModal={this.onSaveModal}
            teacherId={teacherId}
            loading={loadingModal}
          />
        }
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManualSchedulePage />
  </Page>
);



