import { request } from 'utils';

const prefix = '/api/organization';

export const createStaff = ({
  staffId,
  fullname,
  gender,
  facultyId,
  subjectGroupId,
  birthdate,
  titleId,
  positionId,
  rankId,
  degreeId,
  isActive
} = {}) =>
  request({
    prefix,
    url: '/Staff',
    method: 'POST',
    params: {},
    data: {
      staffId,
      fullname,
      gender,
      facultyId,
      subjectGroupId,
      birthdate,
      titleId,
      positionId,
      rankId,
      degreeId,
      isActive
    }
  });


export const updateStaff = ({
  staffId,
  fullname,
  gender,
  facultyId,
  subjectGroupId,
  birthdate,
  titleId,
  positionId,
  rankId,
  degreeId
} = {}) =>
  request({
    prefix,
    url: '/Staff',
    method: 'PUT',
    params: {},
    data: {
      staffId,
      fullname,
      gender,
      facultyId,
      subjectGroupId,
      birthdate,
      titleId,
      positionId,
      rankId,
      degreeId
    }
  });



export const getStaffById = (id) =>
  request({
    prefix,
    url: '/Staff/GetById',
    method: 'GET',
    params: {id},
    data: {}
  });


export const getStaffCategory = ({
  categoryId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Staff/StaffCategory',
    method: 'GET',
    params: {
      categoryId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const getStaffWithStructure = ({
  fullname,
  gender,
  facultyId,
  subjectGroupId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Staff/GetWithStructure',
    method: 'GET',
    params: {fullname,gender,facultyId,subjectGroupId,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });


export const getStaffs = ({
  fullname,
  gender,
  facultyId,
  subjectGroupId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Staff',
    method: 'GET',
    params: {
      fullname,
      gender,
      facultyId,
      subjectGroupId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });
    

export const deleteStaff = id =>
  request({
    prefix,
    url: '/Staff',
    method: 'DELETE',
    params: { },
    data: { id }
  });


export const uploadFile = formData =>
  request({
    prefix,
    url: '/Staff/UploadFile',
    method: 'POST',
    params: {},
    data: formData
  });


export const saveMassStaffs = data =>
  request({
    prefix,
    url: '/Staff/MassSaveStaff',
    method: 'POST',
    params: {},
    data
  });
  



