import React, { Component } from 'react';
import { Form, Row, Modal, Spin } from 'antd';
import { SelectAjax } from 'components/common';
import { getListSubject } from 'services/Subject';


@Form.create({ name: 'FormDuplicatePlan' })
class ModalAddNewTeacherSubject extends Component {

  onSaveSubject = async (e) => {
    e.preventDefault();
    await this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      console.log(values);
      this.props.onSaveModal(values);
    })
  }

  render() {
    const { closeModal, visible, loading } = this.props;
    const { getFieldDecorator, } = this.props.form;
    const optionFieldDecorator = {
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Môn học!',
          },
        ],
      }
    };
    return (
      <Spin spinning={loading}>
        <Modal
          title='Thêm môn học cho giảng viên'
          visible={visible}
          onOk={this.onSaveSubject}
          onCancel={closeModal}
          okText="Lưu"
          cancelText="Hủy"
          width={500}
        >
          <Form className="form-inline-label" layout="vertical" >
            <Row gutter={24}>
              <Form.Item label="Môn học">
                {getFieldDecorator('subjectId', optionFieldDecorator.subjectId)(
                  <SelectAjax
                    service={getListSubject}
                  />
                )}
              </Form.Item>
            </Row>
          </Form>
        </Modal>
      </Spin>
    );
  }
}

export default ModalAddNewTeacherSubject;



