import { request } from 'utils';

const prefix = '/api/education';

export const getStudentSubject = ({
  teacherId,
  subjectId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Teacher_Subject',
    params: {
      teacherId,
      subjectId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const getStudentSubjectById = ({
  teacherId,
}) =>
  request({
    prefix,
    url: `/Teacher_Subject/GetById`,
    params: {
      teacherId,
    },
    method: 'GET',
  });

export const createTeacherSubject = data =>
  request({
    prefix,
    url: '/Teacher_Subject',
    method: 'POST',
    data,
  });



